import React, { FC } from "react";
import { formatPrice } from "../../utils/format";
import {
  StyledCompoundPrice,
  CompoundPriceTitle,
  CompoundPriceLocalNumbers,
  CompoundPriceTollFreeNumbers,
  FullPrice,
  CalculatorRowPrice,
} from "./styled";
import { Text } from "../common";

interface Props {
  title: string | React.ReactElement;
  currentPrices: {
    localNumbersPrice: number;
    localOutboundSmsPrice: number;
    localOutboundMmsPrice: number;
    tollFreeNumbersPrice: number;
    tollFreeOutboundSmsPrice: number;
    tollFreeOutboundMmmPrice: number;
    fullPrice: number;
  };
  comparablePrices: {
    localNumbersPrice: number;
    localOutboundSmsPrice: number;
    localOutboundMmsPrice: number;
    tollFreeNumbersPrice: number;
    tollFreeOutboundSmsPrice: number;
    tollFreeOutboundMmmPrice: number;
    fullPrice: number;
  },
  isBenefical: boolean;
}

const CompoundPrice: FC<Props> = (props) => {
  const { title, currentPrices, isBenefical, comparablePrices } = props;

  const {
    localNumbersPrice,
    localOutboundSmsPrice,
    localOutboundMmsPrice,
    tollFreeNumbersPrice,
    tollFreeOutboundMmmPrice,
    tollFreeOutboundSmsPrice,
    fullPrice
  } = currentPrices;

  return (
    <StyledCompoundPrice background={isBenefical}>
      <CompoundPriceTitle>{title}</CompoundPriceTitle>
      <CompoundPriceLocalNumbers>
        <p>${formatPrice(localNumbersPrice, comparablePrices.localNumbersPrice)}</p>
        <p>${formatPrice(localOutboundSmsPrice, comparablePrices.localOutboundSmsPrice)}</p>
        <p>${formatPrice(localOutboundMmsPrice, comparablePrices.localOutboundMmsPrice)}</p>
      </CompoundPriceLocalNumbers>
      <CompoundPriceTollFreeNumbers>
        <p>${formatPrice(tollFreeNumbersPrice, comparablePrices.tollFreeNumbersPrice)}</p>
        <p>${formatPrice(tollFreeOutboundSmsPrice, comparablePrices.tollFreeOutboundSmsPrice)}</p>
        <p>${formatPrice(tollFreeOutboundMmmPrice, comparablePrices.tollFreeOutboundMmmPrice)}</p>
      </CompoundPriceTollFreeNumbers>
      <FullPrice>
        <CalculatorRowPrice>
          <Text.Regular.Bold.md>$</Text.Regular.Bold.md>
          <Text.Regular.Bold.xl>{formatPrice(fullPrice, comparablePrices.fullPrice)}</Text.Regular.Bold.xl>
        </CalculatorRowPrice>
        <Text.Regular.Bold.md>per month</Text.Regular.Bold.md>
      </FullPrice>
    </StyledCompoundPrice>
  );
};

export default CompoundPrice;
