import {CalculatorCountingRow, CalculatorCountingSection, CalculatorInputWrapper} from "./styled";
import {NumberInput, Text} from "../common";
import React from "react";

type CalculatorSectionProps = {
  numbersTitle: string;
  numberCountOnChange: (value : number | null) => void;
  smsCountOnChange: (value : number | null) => void;
  mmsCountOnChange: (value : number | null) => void;
  numberCountInitialValue: number;
  smsCountInitialValue: number;
  mmsCountInitialValue: number;
};

const CalculatorSection = (
  {
    numbersTitle,
    numberCountOnChange,
    smsCountOnChange,
    mmsCountOnChange,
    numberCountInitialValue,
    smsCountInitialValue,
    mmsCountInitialValue
  } : CalculatorSectionProps) => (
  <CalculatorCountingSection>
    <CalculatorCountingRow>
      <Text.Regular.Bold.md>
        {numbersTitle}
      </Text.Regular.Bold.md>
      <CalculatorInputWrapper>
        <NumberInput onChange={numberCountOnChange} initialValue={numberCountInitialValue}/>
      </CalculatorInputWrapper>
    </CalculatorCountingRow>
    <CalculatorCountingRow>
      <Text.Regular.Normal.md>
        Outbound SMS
      </Text.Regular.Normal.md>
      <CalculatorInputWrapper>
        <NumberInput onChange={smsCountOnChange} initialValue={smsCountInitialValue}/>
      </CalculatorInputWrapper>
    </CalculatorCountingRow>
    <CalculatorCountingRow>
      <Text.Regular.Normal.md>
        Outbound MMS
      </Text.Regular.Normal.md>
      <CalculatorInputWrapper>
        <NumberInput onChange={mmsCountOnChange} initialValue={mmsCountInitialValue}/>
      </CalculatorInputWrapper>
    </CalculatorCountingRow>
  </CalculatorCountingSection>
);

export default CalculatorSection;
