import {PricingItem} from "../types";

export const calculatePrice = (pricingList: PricingItem[], count: number) => {
  let sum = 0;
  let countLeft = count;

  for (let i = 0; i < pricingList.length; i++) {
    const current = pricingList[i];

    if (current.limit !== null && countLeft > current.limit) {
      sum += current.limit * current.price;
      countLeft -= current.limit;
    } else {
      sum += countLeft * current.price;
      break;
    }
  }

  return sum;
}
