import React, { useEffect, useState } from "react";
import { calculatePrice } from "../../utils/calculatePrice";
import {
  COMPETITOR_10DLC_MMS_SENT,
  COMPETITOR_10DLC_NUMBERS_PURCHASED,
  COMPETITOR_10DLC_SMS_SENT,
  COMPETITOR_TOLLFREE_MMS_SENT,
  COMPETITOR_TOLLFREE_NUMBERS_PURCHASED,
  COMPETITOR_TOLLFREE_SMS_SENT,
  TELGORITHM_10DLC_MMS_SENT,
  TELGORITHM_10DLC_NUMBERS_PURCHASED,
  TELGORITHM_10DLC_SMS_SENT,
  TELGORITHM_TOLLFREE_MMS_SENT,
  TELGORITHM_TOLLFREE_NUMBERS_PURCHASED,
  TELGORITHM_TOLLFREE_SMS_SENT,
} from "../../data/pricingList";
import {
  CalculatorBlock,
  CalculatorBody,
  CalculatorCompetitorPriceRow,
  CalculatorDataInput,
  CalculatorDataOutput,
  CalculatorDescription,
  CalculatorOurPriceRow,
  CalculatorPriceSection,
  CalculatorRowPrice,
  PromotionButton,
  PromotionButtonDesktopContainer,
  TitleWithLogo,
} from "./styled";
import { Divider, Text } from "../common";
import { formatPrice } from "../../utils/format";
import { CalculatorSection } from "./index";
import CompoundPrice from "./CompoundPrice";
import CustomLink from "../common/Link";
import { toFixedForNumbers } from "../../utils/round";
import TelgorithmLogo from "../../images/Logo/Telgorithm/MobileLogo.svg"

const PriceCalculator = () => {
  // Our prices
  const [our10DlcNumbersPrice, setOur10DlcNumbersPrice] = useState<number>(0);
  const [our10DlcSmsPrice, setOur10DlcSmsPrice] = useState<number>(0);
  const [our10DlcMmsPrice, setOur10DlcMmsPrice] = useState<number>(0);
  const [ourTollFreeNumbersPrice, setOurTollFreeNumbersPrice] =
    useState<number>(0);
  const [ourTollFreeSmsPrice, setOurTollFreeSmsPrice] = useState<number>(0);
  const [ourTollFreeMmsPrice, setOurTollFreeMmsPrice] = useState<number>(0);

  // Competitor prices
  const [competitor10DlcNumbersPrice, setCompetitor10DlcNumbersPrice] =
    useState<number>(0);
  const [competitor10DlcSmsPrice, setCompetitor10DlcSmsPrice] =
    useState<number>(0);
  const [competitor10DlcMmsPrice, setCompetitor10DlcMmsPrice] =
    useState<number>(0);
  const [competitorTollFreeNumbersPrice, setCompetitorTollFreeNumbersPrice] =
    useState<number>(0);
  const [competitorTollFreeSmsPrice, setCompetitorTollFreeSmsPrice] =
    useState<number>(0);
  const [competitorTollFreeMmsPrice, setCompetitorTollFreeMmsPrice] =
    useState<number>(0);

  const handle10DlcNumbersChange = (value: number | null) => {
    setOur10DlcNumbersPrice(
      calculatePrice(TELGORITHM_10DLC_NUMBERS_PURCHASED, value ?? 0)
    );
    setCompetitor10DlcNumbersPrice(
      calculatePrice(COMPETITOR_10DLC_NUMBERS_PURCHASED, value ?? 0)
    );
  };
  const handle10DlcSmsChange = (value: number | null) => {
    setOur10DlcSmsPrice(calculatePrice(TELGORITHM_10DLC_SMS_SENT, value ?? 0));
    setCompetitor10DlcSmsPrice(
      calculatePrice(COMPETITOR_10DLC_SMS_SENT, value ?? 0)
    );
  };
  const handle10DlcMmsChange = (value: number | null) => {
    setOur10DlcMmsPrice(calculatePrice(TELGORITHM_10DLC_MMS_SENT, value ?? 0));
    setCompetitor10DlcMmsPrice(
      calculatePrice(COMPETITOR_10DLC_MMS_SENT, value ?? 0)
    );
  };
  const handleTollFreeNumbersChange = (value: number | null) => {
    setOurTollFreeNumbersPrice(
      calculatePrice(TELGORITHM_TOLLFREE_NUMBERS_PURCHASED, value ?? 0)
    );
    setCompetitorTollFreeNumbersPrice(
      calculatePrice(COMPETITOR_TOLLFREE_NUMBERS_PURCHASED, value ?? 0)
    );
  };
  const handleTollFreeSmsChange = (value: number | null) => {
    setOurTollFreeSmsPrice(
      calculatePrice(TELGORITHM_TOLLFREE_SMS_SENT, value ?? 0)
    );
    setCompetitorTollFreeSmsPrice(
      calculatePrice(COMPETITOR_TOLLFREE_SMS_SENT, value ?? 0)
    );
  };
  const handleTollFreeMmsChange = (value: number | null) => {
    setOurTollFreeMmsPrice(
      calculatePrice(TELGORITHM_TOLLFREE_MMS_SENT, value ?? 0)
    );
    setCompetitorTollFreeMmsPrice(
      calculatePrice(COMPETITOR_TOLLFREE_MMS_SENT, value ?? 0)
    );
  };

  useEffect(() => {
    handle10DlcNumbersChange(1000);
    handle10DlcSmsChange(2000000);
    handleTollFreeNumbersChange(1000);
    handleTollFreeSmsChange(800000)
  },[])

  // console.log('our10DlcNumbersPrice', our10DlcNumbersPrice,
  //   'our10DlcSmsPrice', our10DlcSmsPrice,
  //   'our10DlcMmsPrice', our10DlcMmsPrice,
  //   'ourTollFreeNumbersPrice', ourTollFreeNumbersPrice,
  //   'ourTollFreeSmsPrice', ourTollFreeSmsPrice,
  //   'ourTollFreeMmsPrice', ourTollFreeMmsPrice)
  const ourPrice =
    our10DlcNumbersPrice +
    our10DlcSmsPrice +
    our10DlcMmsPrice +
    ourTollFreeNumbersPrice +
    ourTollFreeSmsPrice +
    ourTollFreeMmsPrice;
  const competitorPrice =
    competitor10DlcNumbersPrice +
    competitor10DlcSmsPrice +
    competitor10DlcMmsPrice +
    competitorTollFreeNumbersPrice +
    competitorTollFreeSmsPrice +
    competitorTollFreeMmsPrice;
  const savePercent =
    Math.round(((1 - ourPrice / competitorPrice) * 100) / 5) * 5; // round to nearest 5

  const telgorithmTitleWithLogo = <TitleWithLogo><img src={TelgorithmLogo}/>Telgorithm</TitleWithLogo>

  return (
    <CalculatorBlock>
      <Text.Title.Bold.md>Pricing Calculator</Text.Title.Bold.md>
      <CalculatorDescription>
        <Text.Regular.Normal.md>
          Use this tool to get a general idea of how much you will be spending
          per month. All prices are estimates.
        </Text.Regular.Normal.md>
      </CalculatorDescription>
      <CalculatorBody>
        <CalculatorDataInput>
          <CalculatorSection
            numbersTitle="Local numbers"
            numberCountOnChange={handle10DlcNumbersChange}
            smsCountOnChange={handle10DlcSmsChange}
            mmsCountOnChange={handle10DlcMmsChange}
            numberCountInitialValue={1000}
            smsCountInitialValue={2000000}
            mmsCountInitialValue={0}
          />
          <CalculatorSection
            numbersTitle="Toll-Free numbers"
            numberCountOnChange={handleTollFreeNumbersChange}
            smsCountOnChange={handleTollFreeSmsChange}
            mmsCountOnChange={handleTollFreeMmsChange}
            numberCountInitialValue={1000}
            smsCountInitialValue={800000}
            mmsCountInitialValue={0}
          />
          <PromotionButtonDesktopContainer>
            <CustomLink address={"/contact-us"}>
              <PromotionButton>
                {savePercent ? `Save about ${savePercent}%` : "Contact us"}
              </PromotionButton>
            </CustomLink>
          </PromotionButtonDesktopContainer>
        </CalculatorDataInput>
        <CalculatorDataOutput>
          <CompoundPrice
            title={telgorithmTitleWithLogo}
            currentPrices={{
              localNumbersPrice: our10DlcNumbersPrice,
              localOutboundSmsPrice: our10DlcSmsPrice,
              localOutboundMmsPrice: our10DlcMmsPrice,
              tollFreeNumbersPrice: ourTollFreeNumbersPrice,
              tollFreeOutboundSmsPrice: ourTollFreeSmsPrice,
              tollFreeOutboundMmmPrice: ourTollFreeMmsPrice,
              fullPrice: ourPrice,
            }}
            comparablePrices={{
              localNumbersPrice: competitor10DlcNumbersPrice,
              localOutboundSmsPrice: competitor10DlcSmsPrice,
              localOutboundMmsPrice: competitor10DlcMmsPrice,
              tollFreeNumbersPrice: competitorTollFreeNumbersPrice,
              tollFreeOutboundSmsPrice: competitorTollFreeSmsPrice,
              tollFreeOutboundMmmPrice: competitorTollFreeMmsPrice,
              fullPrice: competitorPrice,
            }}
            isBenefical={true}
          />
          <CompoundPrice
            title="Leading Competitor"
            currentPrices={{
              localNumbersPrice: competitor10DlcNumbersPrice,
              localOutboundSmsPrice: competitor10DlcSmsPrice,
              localOutboundMmsPrice: competitor10DlcMmsPrice,
              tollFreeNumbersPrice: competitorTollFreeNumbersPrice,
              tollFreeOutboundSmsPrice: competitorTollFreeSmsPrice,
              tollFreeOutboundMmmPrice: competitorTollFreeMmsPrice,
              fullPrice: competitorPrice,
            }}
            comparablePrices={{
              localNumbersPrice: our10DlcNumbersPrice,
              localOutboundSmsPrice: our10DlcSmsPrice,
              localOutboundMmsPrice: our10DlcMmsPrice,
              tollFreeNumbersPrice: ourTollFreeNumbersPrice,
              tollFreeOutboundSmsPrice: ourTollFreeSmsPrice,
              tollFreeOutboundMmmPrice: ourTollFreeMmsPrice,
              fullPrice: ourPrice,
            }}
            isBenefical={false}
          />
        </CalculatorDataOutput>
      </CalculatorBody>
      <Divider />
      <CalculatorPriceSection>
        <CalculatorOurPriceRow>
          <Text.Regular.ExtraBold.md>{telgorithmTitleWithLogo}</Text.Regular.ExtraBold.md>
          <CalculatorRowPrice>
            <Text.Regular.Bold.md>$</Text.Regular.Bold.md>
            <Text.Regular.Bold.xl>{formatPrice(ourPrice, competitorPrice)}</Text.Regular.Bold.xl>
            <Text.Regular.Bold.md>/mo</Text.Regular.Bold.md>
          </CalculatorRowPrice>
        </CalculatorOurPriceRow>
        <CalculatorCompetitorPriceRow>
          <Text.Regular.ExtraBold.md>
            Leading Competitor
          </Text.Regular.ExtraBold.md>
          <CalculatorRowPrice>
            <Text.Regular.Bold.md>$</Text.Regular.Bold.md>
            <Text.Regular.Bold.xl>
              {formatPrice(competitorPrice, ourPrice)}
            </Text.Regular.Bold.xl>
            <Text.Regular.Bold.md>/mo</Text.Regular.Bold.md>
          </CalculatorRowPrice>
        </CalculatorCompetitorPriceRow>
        <CustomLink address={"/contact-us"}>
          <PromotionButton>
            {savePercent ? `Save about ${savePercent}%` : "Contact us"}
          </PromotionButton>
        </CustomLink>
      </CalculatorPriceSection>
    </CalculatorBlock>
  );
};

export default PriceCalculator;
